<template>
  <div class="user mescroll">
    <div class="top">
      <div class="userInfo">
        <!-- @click="$router.push({name:'personal'})" -->
        <img class="pic" :src="headPic?$utils.dealImg(headPic):require('@/assets/img/default/userDefault.png')" alt />
        <div class="flex-mid">
          <p class="nickname">{{realName || "Mark_oz"}}</p>
          <!-- <p class="role">{{level == 0?'普通会员':''}}</p> -->
        </div>
        <!-- <img class="icon" src="../../assets/img/icon/left.png" alt /> -->
      </div>
      <div class="labels">
        <div class="label" @click="tap(0)">
          <p class="num">{{accountList[0]?accountList[0].acAmt.toFixed(2):'0.00'}}</p>
          <p class="text">我的佣金</p>
        </div>
        <div class="label" @click="tap(1)">
          <p class="num">{{accountList[1]?accountList[1].acAmt.toFixed(2):'0.00'}}</p>
          <p class="text">我的提现</p>
        </div>
        <div class="label" @click="tap(2)">
          <p class="num">{{accountList[2]?accountList[2].acAmt:'0'}}</p>
          <p class="text">我的粉丝</p>
        </div>
      </div>
    </div>
    <div class="mid">
      <p class="title">我的订单</p>
      <div class="list">
        <div class="item" @click="$router.push({name:'activityOrderList'})">
          <img src="../../assets/img/icon/order_all.png" alt />
          <p>全部订单</p>
        </div>
        <div class="item" @click="$router.push({name:'activityOrderList',query:{index:1}})">
          <img src="../../assets/img/icon/order_pay.png" alt />
          <p>待支付</p>
        </div>
        <div class="item" @click="$router.push({name:'activityOrderList',query:{index:2}})">
          <img src="../../assets/img/icon/order_delivery.png" alt />
          <p>待发货</p>
        </div>
        <div class="item" @click="$router.push({name:'activityOrderList',query:{index:4}})">
          <img src="../../assets/img/icon/order_complete.png" alt />
          <p>已完成</p>
        </div>
      </div>
      <div class="bottom">
        <p class="title">我的应用</p>
        <div class="list">
          <div class="item" v-for="(item,index) in list" :key="index" @click="to(item.router)" v-show="item.empType<= empType">
            <img :src="item.icon" alt />
            <p>{{item.text}}</p>
          </div>
        </div>
      </div>
      <div class="technical" v-if="level <= 1 && false">
        <div class="flex" @click="add">
          <img class="icon" src="../../assets/img/activity/icon.png" alt="">
          <p class="text">
            我要开通店铺
            <span>》》</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getActivityHome, wxApi } from "@/api";
export default {
  data() {
    return {
      realName: "",
      headPic: "",
      userCode: "",
      empType: 0,
      encourage: 0,
      voucher: 0,
      balance: 0,
      list: [
        {
          text: "收货地址",
          icon: require("@/assets/img/activity/addr.png"),
          router: { name: "myAddress" },
          empType: -1,
        },
        {
          text: "活动收藏",
          icon: require("@/assets/img/activity/icon2.png"),
          router: { name: "activityCollectList" },
          empType: -1,
        },
        {
          text: "我的活动",
          icon: require("@/assets/img/activity/activity.png"),
          router: { name: "myActivity" },
          empType: -1,
        },
        {
          text: "活动管理",
          icon: require("@/assets/img/activity/management.png"),
          router: { name: "activityManagement" },
          empType: 3,
        },
        {
          text: "订单管理",
          icon: require("@/assets/img/activity/order.png"),
          router: { name: "activityOrderManagement" },
          empType: 2,
        },
        {
          text: "我的收益",
          icon: require("@/assets/img/activity/money.png"),
          router: {
            name: "earnings",
            query: { type: "2" },
          },
          empType: 2,
        },
        {
          text: "核销订单",
          icon: require("@/assets/img/activity/icon1.png"),
          router: { name: "closeActivityOrderList" },
          empType: 1,
        },
      ],
      accountList: [],
      level: 0,
    };
  },
  methods: {
    tap(index) {
      if (index == 0 || index == 1) {
        this.$router.push({
          name: "bonus",
          query: {
            code: this.accountList[index].ptCode,
            name: this.accountList[index].ptName,
          },
        });
      } else if (index == 2) {
        // this.$router.push({
        //   name: "activityTeam"
        // });
      } else {
        console.log(11);
      }
    },
    to(router) {
      if (router.name) {
        this.$router.push(router);
      } else {
        this.scanQRCode();
      }
    },
    scanQRCode() {
      let that = this;
      wxApi()
        .then(res => {
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["scanQRCode"],
          });
          this.$wx.ready(function() {
            that.$wx.scanQRCode({
              needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
              scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
              success: function(res) {
                var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                console.log(res.resultStr);
              },
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    add() {
      this.$router.push({ name: "merchantsManagement", query: { type: "1" } });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getActivityHome().then(res => {
        loading.clear();
        console.log(res);
        if (res.code == 0) {
          this.accountList = res.data.dataList;
          this.realName = res.data.userVip.userName;
          this.headPic = res.data.userVip.userHeader;
          this.level = parseInt(res.data.userVip.userType);
          this.empType = parseInt(res.data.userVip.empType);
          sessionStorage.setItem("userCode", res.data.userVip.userCode);
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = this.$config.activeTitle;
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.user {
  height: calc(100vh - 50px);
  .top {
    background: url("../../assets/img/pic/userBg.png") no-repeat;
    background-size: 100% 100%;
    .userInfo {
      // display: flex;
      // align-items: center;
      text-align: center;
      padding: 20px 10px;
      .pic {
        width: 60px;
        height: 60px;
        // margin-right: 10px;
        border-radius: 50%;
      }
      .flex-mid {
        flex: 2;
        .nickname {
          font-size: 18px;
          line-height: 30px;
          margin-top: 5px;
          // margin-right: 15px;
        }
        .role {
          display: inline-block;
          background: url("../../assets/img/icon/roleBg.png") no-repeat;
          background-size: 100% 100%;
          font-size: 12px;
          color: #fff;
          line-height: 25px;
          padding: 0 10px;
          margin: 5px 0 0;
        }
      }
      .icon {
        width: 7px;
      }
    }
    .labels {
      padding: 10px;
      display: flex;
      align-items: center;
      .label {
        flex: 1;
        text-align: center;
        .num {
          font-weight: bold;
          line-height: 30px;
        }
        .text {
          font-size: 12px;
          line-height: 25px;
          color: #999;
        }
      }
    }
  }
  .mid {
    padding: 20px 10px;
    background: #fff;
    .title {
      font-size: 18px;
      font-weight: 600;
      line-height: 30px;
      color: #333;
    }
    .list {
      text-align: center;
      margin: 20px 0 0;
      .item {
        display: inline-block;
        width: 25%;
        img {
          width: 20px;
        }
        p {
          font-size: 14px;
          line-height: 25px;
        }
      }
    }
  }
  .bottom {
    margin: 10px 0 0;
    background: #fff;
    .title {
      font-size: 18px;
      font-weight: 600;
      line-height: 30px;
    }
    .list {
      margin: 20px 0 0;
      text-align: left;
      .item {
        display: inline-block;
        width: 25%;
        text-align: center;
        margin: 0 0 20px;
        img {
          height: 25px;
        }
        p {
          font-size: 14px;
          line-height: 25px;
          color: #666;
        }
      }
    }
  }
  .technical {
    text-align: center;
    .flex {
      display: flex;
      align-items: center;
      justify-content: center;
      .icon {
        width: 25px;
        margin-right: 5px;
      }
      .text {
        line-height: 30px;
        font-size: 15px;
        color: #f56873;
        border-bottom: 1pt solid #f56873;
        font-weight: bold;
        span {
          margin-left: 5px;
          letter-spacing: -5px;
        }
      }
    }
  }
}
</style>
